
.bg {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    position: relative;
    width: 100%;
    height: 100%; /* Set full viewport height */
    overflow: hidden;
    display: flex;
    flex-direction: column; /* Stack children vertically */
}


.flexbox-two {
    flex: 1; /* Take the remaining space */
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: auto; /* Enable scrolling if content overflows */
}

  
.bg::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(242, 219, 176, 1) 20%, rgba(218, 218, 218, 0.8) 70%, rgba(255, 235, 188, 1) 100%);
    mix-blend-mode: overlay;
    z-index: -10;
}
  
.bg-doodle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/bg.webp');
    opacity: 15%;
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
}

.soulstories-logo {
    position: relative;
    top: 35px; 
    left: 50%; 
    transform: translateX(-50%); 
    max-width: 90%; 
    max-height: 60px;
    z-index: 1;
}

.search-container {
    position: relative;
    top: 50px; /* Adjust the top position as needed */
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    border: 2px solid #003333;
    background-color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); 
    border-radius: 20px;
    padding: 3px;
    padding-left: 7px;
    width: 300px; /* Adjust the width as needed */
    max-width: 80%; /* Limit the maximum width to 80% of the container width */
}

.search-input {
    flex: 1;
    border: none;
    outline: none;
    padding: 5px; /* Adjust padding to accommodate the icon */
    padding-right: 30px; /* Ensure space for the icon */
    box-sizing: border-box; /* Include padding in the width calculation */
    border-radius: 20px;
    width: 100%; /* Take up remaining width */
}

.search-icon {
    position: relative;
    width: 20px; 
    height: 20px;
    margin-left: 5px; 
    padding-right: 10px;
    cursor: pointer; 
}

.scrollable-container {
    position: relative;
    top: 70px; /* Adjust the top position as needed */
    left: 50%;
    transform: translateX(-50%);
    width: 90%; /* Adjust the width as needed */
    max-width: 600px; /* Set maximum width for the container */
    overflow-x: auto; /* Enable horizontal scrolling */
    white-space: nowrap; /* Prevent divs from wrapping */
    display: flex;
    z-index: 1000;
    /* padding: 10px; */
}
.scrollable-container::-webkit-scrollbar {
    display: none;
}
.scrollable-content {
    display: flex;
}

.food-container {
    background-color: rgba(240, 240, 240, 0.5);
    border-radius: 20px;
    top: 10px;
    width: 90%;
    height: 70%;
    margin: 10px auto; /* Adjust the margin as needed */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.3);
    position: relative;
}

.food-text-img {
    position: absolute; 
    top: -16px; 
}

.food-text {
    position: absolute; 
    top: 0px; 
    height: 50px;
    max-width: 90px;
    font-family: "Poppins", sans-serif;
    font-size: 12px; 
    font-weight: bold; 
    text-align: center;
    color: white;
    z-index: 2;
}

.food-items-container{
    position: relative;
    top: 20px;
    height: 80%;
    width: 95%;
    overflow-y: auto;
}
.food-items-container::-webkit-scrollbar {
    width: 4px;
  }
  /* Track */
  .food-items-container::-webkit-scrollbar-track {
    background: #003333; /* Change track color */
  }
  /* Handle */
  .food-items-container::-webkit-scrollbar-thumb {
    background: #E7C16D; /* Change thumb color */
    border-radius: 3px;
  }
  /* Handle on hover */
  .food-items-container::-webkit-scrollbar-thumb:hover {
    background: #003333;
  }


.placeorder {

    background-color: #090909;
    position: relative;
    border: none;
    color: white;
    padding: 15px 20px;
    text-align: center;
    text-decoration: none;
    display: block;
    width: 40%;
    max-width: 200px;
    min-width: 100px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 20px;
    position: fixed;
    bottom: 3%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3); 
}




