.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure the popup is above other content */
}

.popup-man{
    position: relative;

    height: 100px;
    width: 100px;
    top: -50px;
}
.popup-content {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 200px;
    width: 80%;
    background-color: white;
    padding: 20px;
    border-radius: 20px;
    /* background-image: url('../images/bg.png'); */
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    text-align: center;
}

.popup-content h1 {
    color: #003333;
    top: -10%;
    font-size: 25px;
    position: relative;
}

.popup-content h2 {
    font-size: 20px;
    position: relative;
    top: -10%;

}

.popup-content button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
}

.popup-content button:hover {
    background-color: #45a049;
}
