/* Add these styles to your CSS file */
@import url('https://fonts.googleapis.com/css2?family=Cormorant+SC:wght@300;400;500;600;700&family=Cormorant:ital,wght@0,300..700;1,300..700&display=swap');

/* Edit Order Button */
.backtoedit {
    position: absolute;
    top: 0px;
    left: 10px;
    background-color: #007474; /* Coral */
    color: rgb(255, 255, 255);
    padding: 5px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 12px;
    margin: 10px 0;
    cursor: pointer;
    border-radius: 10px;
}

.backtoedit:hover {
    background-color: #ff7256; /* Lighter Coral */
}

.selected-foods-container {
    position: relative;
    font-family: "Cormorant SC", serif;
    font-weight: 700;
    font-style: normal;
    font-size: 15px;
    height: 70%;
    width: 85%;
}

.selected-food-header {
    
    color: rgb(0, 0, 0);
    font-size: 16px;
    font-weight: 900;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; /* Three columns */
    padding: 10px;
    padding-top: 20px;
    border-radius: 10px;
    font-weight: bold;
}

.selected-food-items{
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255, 0.3);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    height: 70%;
}
.selected-food-items-2{
    
    transform: translateY(-50%);
    top: 50%;
    position: relative;
    overflow: auto;
    height: 90%;
}
.selected-food-items::-webkit-scrollbar {
    width: 4px;
  }
  /* Track */
  .selected-food-items::-webkit-scrollbar-track {
    background: #003333; /* Change track color */
  }
  /* Handle */
  .selected-food-items::-webkit-scrollbar-thumb {
    background: #E7C16D; /* Change thumb color */
    border-radius: 3px;
  }
  /* Handle on hover */
  .selected-food-items::-webkit-scrollbar-thumb:hover {
    background: #003333;
  }



.selected-food {
    
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; /* Three columns */
    padding: 10px;
}

.total-container {
    height: 50px;
    display: flex;
    flex-direction: column;
    position: relative;
    bottom: 20px;
    width: 80%;
    font-family: "Cormorant SC", serif;
    font-weight: 900;
    font-size: 18px;
    font-style: normal;
}
.grand-total {
    display: flex;

    justify-content: space-between;
    padding: 3px;
    font-weight: bold;
}

.food-quantity-header{
    text-align: center;
    
}
.food-price-header{
    text-align: right;
}
.food-name {
    grid-column: 1 / span 1;
}

.food-quantity {
    grid-column: 2 / span 1;
    text-align: center;
}

.food-price {
    grid-column: 3 / span 1;
    text-align: right;
}
