.food-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    position: relative; /* Ensure relative positioning */
}

.food-item-name {
    flex: 1;
    font-family: "Cormorant", serif;
    font-optical-sizing: auto;
    font-size: 14px;
    font-weight: 700;
    font-style: normal;
}

.food-quantity-container {

    position: absolute;
    top: 50%;
    right: 40px;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    background-color: #003334;
    border-radius: 5px;
    padding: 0;
}

.quantity-button {
    padding: 3px 5px ;
    cursor: pointer;
    margin: 0 5px;
    color: #FFFFFF;
}

.selected-quantity {
    font-size: 12px;
    font-weight: bold;
    color: #FFFFFF;
}

.price {
    font-weight: bold;
    font-family: "Cormorant", serif;
}

.add-button{
    margin-right: 10px;
    margin-left: 10px;
    padding: 5px 10px ;
    font-size: 12px;
    font-weight: bold;
    color: #FFFFFF;
}